import { Component, OnInit } from '@angular/core';

import { AlertService } from '../shared/services/alert.service';
import { EndpointsService } from '../endpoints.service';
@Component({
  templateUrl: 'dashboard.component.html'
})
export class DashboardComponent implements OnInit {

  title = 'Dashboard';  // ToDo recheck need

  // public status: { isopen: boolean } = { isopen: false };

  constructor(private alertService: AlertService, public endpointsService: EndpointsService) {
  }


  currentUser = JSON.parse(localStorage.getItem('currentUser'))[0];

  // public toggleDropdown($event:MouseEvent):void {
  //   $event.preventDefault();
  //   $event.stopPropagation();
  //   this.status.isopen = !this.status.isopen;
  // }

 solicitudesRegistro : number;
   solicitudesRutina : number;
   centroSeleccionado = '';
centrosUsuario =[];
  public menuItems: any[];
seleccionCentro=false;

  ngOnInit(): void {


   this.getCentrosUsuario();

    //this.alertService.success('Bienvenido');
    // this.alertService.info('info');
    // this.alertService.warning('warning');
    // this.alertService.danger('danger');
    // this.alertService.primary('primary');
    // this.alertService.secondary('secondary');
    // this.alertService.light('light');
    // this.alertService.dark('dark');
  }



    getCentrosUsuario(){
    let datE = {idUsuario:this.currentUser['idUsuarioConsola']};
        this.endpointsService.getCentrosUsuario(datE)
    .then((data:any) => {    
      this.centrosUsuario = data;
      let nombre = '';
       data.forEach((item)=>{

        if(item.idCentro == localStorage.getItem( 'idCentroBY')){
        nombre= item.nombre;


        }
        
      });
      if(nombre !== ''){
      this.centroSeleccionado = nombre;
        this.seleccionCentro = true;
      }
      else{
      console.log('centroNOselec');
      this.seleccionCentro = false;
      }
      
    console.log(this.centrosUsuario );
     
     });


  }



cambiarCentro(idCentro, nombre){
  
  this.centroSeleccionado = nombre;
    localStorage.setItem( 'idCentroBY', idCentro );
      window.location.reload();
    console.log(localStorage.getItem( 'idCentroBY'));
}



}



